





















import { Component, Vue, Watch } from 'vue-property-decorator';
import { Route } from 'vue-router';
import TodoListView from './TodoListView.vue';
import Popup from '../components/popup.vue';
import Storage from '../utils/storage';
import { Getter, Action } from 'vuex-class';
import { TodoItem, TODO_LIST } from '../store';

@Component({
  components: { Popup, TodoListView },
})
export default class Main extends Vue {
  private active: number = 0
  private nowTitle: string = '待办'
  private nowStatus?: boolean = false

  private show: boolean = false

  // @Getter('getList') getterList?: TodoItem[]
  @Getter('getList') private getterList!: (status?: boolean) => TodoItem[]
  @Action('loadList') private actionLoadList(data: TodoItem[]): void {/**/}
  @Action('clearCompleted') private actionClearCompleted(): void {/**/}

  get todoCount(): number {
    return this.getterList(false).length
  }
  get doneCount(): number {
    return this.getterList(true).length
  }

  @Watch('$route', { immediate: true, deep: true })
  private onRouteChange(route: Route) {
    switch (route.params.status) {
      case 'todo':
        this.nowStatus = false
        break;
      case 'done':
        this.nowStatus = true
        break;
      case 'all':
        this.nowStatus = undefined
        break;
      default:
        break;
    }
  }


  // 生命周期
  private created(): void {
    const list = Storage.get<TodoItem>(TODO_LIST)
    if (list) {
      this.actionLoadList(list)
    }
    this.changeTabBarActive()
  }

  private updated(): void {
    this.changeTabBarActive()
  }

  // 自定义方法
  private openModal(): void {
    this.show = true
  }

  private closeModal(): void {
    this.show = false
  }

  private clearCompleted(): void {
    this.actionClearCompleted()
  }

  private changeTabbar(active: number): void {
    switch (active) {
      case 0:
        this.$router.push({ path: '/main/todo' })
        break
      case 1:
        this.$router.push({ path: '/main/done' })
        break
      case 2:
        this.$router.push({ path: '/main/all' })
        break
      default:
        break
    }
  }

  // 用户手动更改路由时，改变当前底部导航高亮
  private changeTabBarActive(): void {
    const routePath = this.$route.path
    switch (routePath) {
      case '/main/todo':
        this.active = 0
        this.nowTitle = '待办'
        break
      case '/main/done':
        this.active = 1
        this.nowTitle = '完成'
        break;
      case '/main/all':
        this.active = 2
        this.nowTitle = '全部'
        break
      default:
        this.active = 0
        break
    }
  }
}
